body {
  margin: 0;
  padding: 0;
  color: #c2bcbc;
  min-width: 320px;
  background-color: #232224;
  font-size: 18px;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

html {
  box-sizing: border-box;
  overflow-y: scroll;
  /* margin-left: calc(100vw - 100%);
  margin-right: 0; */ }

*,
*::before,
*::after {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  color: #fff; }

p {
  margin: 0;
  padding: 0; }

ul {
  padding: 0;
  margin: 0; }

li {
  list-style: none; }

.container {
  max-width: 480px;
  padding: 0 10px;
  margin: 0 auto; }
  .container h2 {
    margin-top: 20px;
    margin-bottom: 20px; }

@media (min-width: 481px) {
  .container {
    max-width: 1280px;
    padding: 0 10px;
    margin: 0 auto; } }

.header {
  width: 100%;
  height: 70px;
  background-color: #09345a; }

.main {
  padding: 70px 0; }

.choise__link {
  display: block;
  margin-bottom: 50px; }
  .choise__link img {
    display: block;
    max-width: 100%;
    margin: 0 auto; }

.survivor__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px; }
  .survivor__list li {
    max-width: 110px;
    height: 170px;
    font-size: 13px;
    border: 1px solid #4d4b4b;
    padding: 5px;
    background-color: #1b1a1a;
    transition: 0.2s; }
    .survivor__list li:active {
      opacity: 0.5; }
    .survivor__list li img {
      max-width: 100%; }
    .survivor__list li p {
      text-align: center; }

.back__link {
  text-decoration: none;
  font-size: 24px;
  color: #ececec;
  width: 100px;
  height: 40px;
  display: block;
  text-align: center;
  padding: 5px;
  border: 1px solid #ececec;
  margin-bottom: 20px;
  margin-top: 20px; }

.description {
  margin-bottom: 20px; }
  .description h3 {
    margin-bottom: 20px; }

.table {
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #dddddd;
  border-collapse: collapse; }

.table th {
  font-weight: bold;
  padding: 5px;
  border: 1px solid #dddddd; }

.table td {
  border: 1px solid #dddddd;
  padding: 5px;
  text-align: center; }

.th-one {
  color: #e8c252; }

.th-two {
  color: mediumseagreen; }

.th-three {
  color: #912cee; }

.feature {
  margin-bottom: 40px; }
  .feature h3 {
    margin-bottom: 20px; }
  .feature li {
    margin-bottom: 10px; }

.note {
  margin-bottom: 20px; }
  .note h3 {
    margin-bottom: 20px; }
  .note li {
    margin-bottom: 10px; }

.all-perks-link {
  display: block;
  text-decoration: none;
  width: 150px;
  height: 50px;
  background-color: #524242;
  border-radius: 5px;
  color: #ececec;
  border: 1px solid #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  text-transform: uppercase;
  padding: 14px;
  opacity: 1;
  transition: 0.2s; }
  .all-perks-link:active {
    opacity: 0.5; }
