body {
  margin: 0;
  padding: 0;
  color: #c2bcbc;
  min-width: 320px;
  background-color: #232224;
  font-size: 18px;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
  box-sizing: border-box;
  overflow-y: scroll;

  /* margin-left: calc(100vw - 100%);
  margin-right: 0; */
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  color: #fff;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}

.container {
  max-width: 480px;
  padding: 0 10px;
  margin: 0 auto;
  h2 {
    margin-top: 20px; // временно !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    margin-bottom: 20px; // временно !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
  }
}

@media (min-width: 481px) {
  .container {
    max-width: 1280px;
    padding: 0 10px;
    margin: 0 auto;
  }
}
